@import 'mixins';

.Accio__Animation__Container {
    width: 100%;
    position: absolute;
    z-index: 2;
    cursor: pointer;
    filter: brightness(0%);
    display: block;
    margin: 0 auto;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    @include for-tablet-landscape-up {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      max-width: 60%;
      max-height: 80%;
    }
}

.Accio__Animation__Message {
  font-weight: 300;
  padding-top: 100px;
  font-size: 1.6rem;
  text-transform: capitalize;
  margin: 0 auto;
  text-align: center;
  opacity: 0;
  animation: 1s fadein ease-out forwards;
  &-is-hidden {
    display: none;
  }
  @include for-tablet-portrait-up {
    font-size: 2.875rem;
  }
}

